* {
  box-sizing: border-box;
}

img {
  width: 100%;
}

.landing {
  position: relative;
  background: url('/img/bg.png') no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(39, 49, 110, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::placeholder {
  color: #bbb !important;
}

.login {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.dashboard,
.add-employee,
.edit-employee {
  height: 100vh;
}
